@use './variables' as v;

@mixin telefono{
    @media (min-width: v.$telefono){
        @content;
    }
}

@mixin tablet{
    @media(min-width: v.$tablet){
        @content;
    }
}

@mixin desktop{
    @media(min-width: v.$desktop){
        @content;
    }
}

@mixin desktopXL{
    @media(min-width: v.$desktopXL){
        @content;
    }
}

@mixin grid($columnas, $espaciado){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin grid($filas, $espaciado){
    display: grid;
    grid-template-columns: repeat($filas, 1fr);
    gap: $espaciado;
}

@mixin resetearLista{
    margin: 0;
    padding: 0;
    list-style: none;
}